"use client"
import Link from 'next/link'
import React, { useEffect, useRef, useState } from 'react'

export default function Header() {
  const [open, setOpen] = useState(false)
  const btnRef = useRef<HTMLButtonElement | null>(null)
  const panelRef = useRef<HTMLDivElement | null>(null)

  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (e.key === 'Escape' && open) setOpen(false)
    }
    window.addEventListener('keydown', onKey)
    return () => window.removeEventListener('keydown', onKey)
  }, [open])

  useEffect(() => {
    if (open) panelRef.current?.focus()
  }, [open])

  const toggle = () => setOpen((v) => !v)
  const close = () => setOpen(false)

  return (
    <header className="sticky top-0 z-50 bg-neutral-900/80 backdrop-blur-sm border-b border-neutral-800">
      <div className="container flex items-center justify-between py-3 md:py-4">
        <div className="flex items-center gap-4">
          <Link href="/" className="text-lg md:text-xl font-semibold">Resonar√© Chamber Choir</Link>
        </div>

        {/* Desktop nav */}
        <nav aria-label="Main navigation" className="hidden md:block">
          <ul className="flex gap-6 items-center text-sm">
            <li><Link href="/" className="hover:text-neutral-200">Home</Link></li>
            <li><Link href="/about" className="hover:text-neutral-200">About RCC</Link></li>
            <li><Link href="/auditions" className="hover:text-neutral-200">Auditions and Rehearsals</Link></li>
            <li><Link href="/artistic-director" className="hover:text-neutral-200">About the Artistic Director</Link></li>
            <li><Link href="/contact" className="hover:text-neutral-200">Contact our Artistic Team</Link></li>
            <li>
              <Link href="/contact#schedule" className="ml-4 px-3 py-2 rounded bg-neutral-700 hover:bg-neutral-600 focus-ring">Schedule Audition</Link>
            </li>
          </ul>
        </nav>

        {/* Mobile controls */}
        <div className="md:hidden flex items-center">
          <button ref={btnRef} aria-controls="mobile-menu" aria-expanded={open} onClick={toggle} className="p-2 rounded focus-ring">
            <span className="sr-only">Menu</span>
            {open ? (
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" aria-hidden>
                <path d="M6 6L18 18M6 18L18 6" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
              </svg>
            ) : (
              <svg width="24" height="24" viewBox="0 0 24 24" fill="none" aria-hidden>
                <path d="M3 6h18M3 12h18M3 18h18" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
              </svg>
            )}
          </button>
        </div>
      </div>

      {/* Mobile menu panel */}
      <div
        id="mobile-menu"
        ref={panelRef}
        role="dialog"
        aria-modal="true"
        tabIndex={-1}
        className={`md:hidden transition-all duration-200 ${open ? 'max-h-screen' : 'max-h-0 overflow-hidden'}`}>
        <div className="border-t border-neutral-800 bg-neutral-900/95">
          <div className="container py-4">
            <ul className="flex flex-col gap-3 text-sm">
              <li><Link href="/" onClick={close} className="block px-2 py-2 rounded hover:bg-neutral-800">Home</Link></li>
              <li><Link href="/about" onClick={close} className="block px-2 py-2 rounded hover:bg-neutral-800">About RCC</Link></li>
              <li><Link href="/auditions" onClick={close} className="block px-2 py-2 rounded hover:bg-neutral-800">Auditions and Rehearsals</Link></li>
              <li><Link href="/artistic-director" onClick={close} className="block px-2 py-2 rounded hover:bg-neutral-800">About the Artistic Director</Link></li>
              <li><Link href="/contact" onClick={close} className="block px-2 py-2 rounded hover:bg-neutral-800">Contact our Artistic Team</Link></li>
              <li><Link href="/contact#schedule" onClick={close} className="mt-2 block px-3 py-2 rounded bg-neutral-700 hover:bg-neutral-600">Schedule Audition</Link></li>
            </ul>
          </div>
        </div>
      </div>
    </header>
  )
}
